/**
 * 
 */
export function formatTime(timestamp: number): string {
  const date = new Date(timestamp);
  const now = new Date();
  const diff = now.getTime() - date.getTime();

  // 
  if (diff < 24 * 60 * 60 * 1000 && now.getDate() === date.getDate()) {
    return date.toLocaleTimeString("zh-CN", { hour: "2-digit", minute: "2-digit", second: "2-digit" });
  }

  // 
  if (now.getFullYear() === date.getFullYear()) {
    return date.toLocaleDateString("zh-CN", { month: "short", day: "numeric", hour: "2-digit", minute: "2-digit" });
  }

  return date.toLocaleString("zh-CN");
}

/**
 * 
 */
export function formatRelativeTime(timestamp: number): string {
  const now = Date.now();
  const diff = now - timestamp;

  if (diff < 1000) return "";
  if (diff < 60000) return `${Math.floor(diff / 1000)}`;
  if (diff < 3600000) return `${Math.floor(diff / 60000)}`;
  if (diff < 86400000) return `${Math.floor(diff / 3600000)}`;
  if (diff < 604800000) return `${Math.floor(diff / 86400000)}`;

  return formatTime(timestamp);
}

/**
 * 
 */
export function formatBytes(bytes: number): string {
  if (bytes === 0) return "0 B";
  const k = 1024;
  const sizes = ["B", "KB", "MB", "GB", "TB"];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  return `${(bytes / Math.pow(k, i)).toFixed(2)} ${sizes[i]}`;
}

/**
 *  JSON
 */
export function formatJSON(obj: any): string {
  try {
    return JSON.stringify(obj, null, 2);
  } catch {
    return String(obj);
  }
}

/**
 * 
 */
export function formatDuration(ms: number): string {
  if (ms < 1000) return `${ms}ms`;
  if (ms < 60000) return `${(ms / 1000).toFixed(2)}s`;
  return `${(ms / 60000).toFixed(2)}m`;
}

/**
 * 
 */
export function truncate(str: string, maxLength: number, suffix = "..."): string {
  if (!str) return "";
  if (str.length <= maxLength) return str;
  return str.slice(0, maxLength - suffix.length) + suffix;
}

/**
 * 
 */
export function formatClient(client: string): string {
  const map: Record<string, string> = {
    claude: "Claude",
    codex: "Codex",
    gemini: "Gemini",
  };
  return map[client] || client;
}

/**
 * 
 */
export function formatStatus(status?: number): string {
  if (!status) return "N/A";
  if (status >= 200 && status < 300) return `${status} `;
  if (status >= 400) return `${status} `;
  return `${status}`;
}

/**
 * 
 */
export function getStatusColor(status?: number): "success" | "warning" | "danger" | "default" {
  if (!status) return "default";
  if (status >= 200 && status < 300) return "success";
  if (status >= 300 && status < 400) return "warning";
  return "danger";
}

/**
 *  UUID
 */
export function generateUUID(): string {
  return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function (c) {
    const r = (Math.random() * 16) | 0;
    const v = c === "x" ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
}
