import React, { useState, useEffect } from "react";
import {
  Input,
  Textarea,
  Select,
  SelectItem,
  Button,
  Checkbox,
  Card,
  CardBody,
  Chip,
  Divider,
} from "@heroui/react";
import { PromptxyRule, PromptxyOp, PromptxyOpType } from "@/types";
import { validateRule, createDefaultRule, generateUUID } from "@/utils";

interface RuleEditorProps {
  rule?: PromptxyRule | null;
  onSave: (rule: PromptxyRule) => void;
  onCancel: () => void;
  onPreview?: (rule: PromptxyRule) => void;
}

const CLIENT_OPTIONS = [
  { value: "claude", label: "Claude" },
  { value: "codex", label: "Codex" },
  { value: "gemini", label: "Gemini" },
];

const FIELD_OPTIONS = [
  { value: "system", label: "System" },
  { value: "instructions", label: "Instructions" },
];

const OP_TYPE_OPTIONS = [
  { value: "set", label: "Set ()" },
  { value: "append", label: "Append ()" },
  { value: "prepend", label: "Prepend ()" },
  { value: "replace", label: "Replace ()" },
  { value: "delete", label: "Delete ()" },
  { value: "insert_before", label: "Insert Before ()" },
  { value: "insert_after", label: "Insert After ()" },
];

export const RuleEditor: React.FC<RuleEditorProps> = ({
  rule,
  onSave,
  onCancel,
  onPreview,
}) => {
  const [formData, setFormData] = useState<PromptxyRule>(rule || createDefaultRule());
  const [validation, setValidation] = useState({ valid: true, errors: [] as string[], warnings: [] as string[] });

  useEffect(() => {
    if (rule) {
      setFormData(rule);
    }
  }, [rule]);

  useEffect(() => {
    const result = validateRule(formData);
    setValidation(result);
  }, [formData]);

  const updateField = (path: string, value: any) => {
    setFormData((prev) => {
      const newData = { ...prev };
      const keys = path.split(".");
      let current: any = newData;
      for (let i = 0; i < keys.length - 1; i++) {
        current[keys[i]] = { ...current[keys[i]] };
        current = current[keys[i]];
      }
      current[keys[keys.length - 1]] = value;
      return newData;
    });
  };

  const addOp = () => {
    const newOp: PromptxyOp = { type: "append", text: "" };
    setFormData((prev) => ({
      ...prev,
      ops: [...prev.ops, newOp],
    }));
  };

  const updateOp = (index: number, updates: any) => {
    setFormData((prev) => ({
      ...prev,
      ops: prev.ops.map((op, i) => (i === index ? ({ ...op, ...updates } as PromptxyOp) : op)),
    }));
  };

  const removeOp = (index: number) => {
    setFormData((prev) => ({
      ...prev,
      ops: prev.ops.filter((_, i) => i !== index),
    }));
  };

  const handleSave = () => {
    if (validation.valid) {
      onSave(formData);
    }
  };

  const handlePreview = () => {
    if (onPreview && validation.valid) {
      onPreview(formData);
    }
  };

  const generateNewId = () => {
    setFormData((prev) => ({ ...prev, id: `rule-${generateUUID()}` }));
  };

  return (
    <div className="max-h-[70vh] overflow-y-auto space-y-4 p-2">
      {/*  */}
      {!validation.valid && (
        <Card className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800">
          <CardBody className="p-3">
            <div className="font-bold text-red-700 dark:text-red-400 mb-1">:</div>
            {validation.errors.map((err, i) => (
              <div key={i} className="text-sm text-red-600 dark:text-red-300">
                 {err}
              </div>
            ))}
          </CardBody>
        </Card>
      )}

      {/*  */}
      {validation.warnings.length > 0 && (
        <Card className="bg-yellow-50 dark:bg-yellow-900/20 border border-yellow-200 dark:border-yellow-800">
          <CardBody className="p-3">
            <div className="font-bold text-yellow-700 dark:text-yellow-400 mb-1">:</div>
            {validation.warnings.map((warn, i) => (
              <div key={i} className="text-sm text-yellow-600 dark:text-yellow-300">
                 {warn}
              </div>
            ))}
          </CardBody>
        </Card>
      )}

      {/*  */}
      <Card className="border border-gray-200 dark:border-gray-700">
        <CardBody className="space-y-3">
          <div className="flex items-center gap-2">
            <h4 className="text-lg font-bold"></h4>
            <Chip color="primary" variant="flat" size="sm"></Chip>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
            <div className="md:col-span-2">
              <Input
                label="ID"
                placeholder="rule-xxx"
                value={formData.id}
                onChange={(e) => updateField("id", e.target.value)}
                isRequired
                radius="lg"
              />
            </div>
            <div>
              <Button
                size="sm"
                variant="flat"
                onPress={generateNewId}
                className="w-full mt-6"
                color="primary"
              >
                UUID
              </Button>
            </div>
          </div>
          <Textarea
            label=" ()"
            placeholder="..."
            value={formData.description || ""}
            onChange={(e) => updateField("description", e.target.value)}
            radius="lg"
            minRows={2}
          />
        </CardBody>
      </Card>

      <Divider />

      {/*  */}
      <Card className="border border-gray-200 dark:border-gray-700">
        <CardBody className="space-y-3">
          <h4 className="text-lg font-bold"> (When)</h4>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
            <Select
              label=""
              selectedKeys={[formData.when.client]}
              onChange={(e) => updateField("when.client", e.target.value)}
              radius="lg"
              isRequired
            >
              {CLIENT_OPTIONS.map((opt) => (
                <SelectItem key={opt.value}>
                  {opt.label}
                </SelectItem>
              ))}
            </Select>
            <Select
              label=""
              selectedKeys={[formData.when.field]}
              onChange={(e) => updateField("when.field", e.target.value)}
              radius="lg"
              isRequired
            >
              {FIELD_OPTIONS.map((opt) => (
                <SelectItem key={opt.value}>
                  {opt.label}
                </SelectItem>
              ))}
            </Select>
            <Input
              label="HTTP ()"
              placeholder="GET, POST..."
              value={formData.when.method || ""}
              onChange={(e) => updateField("when.method", e.target.value || undefined)}
              radius="lg"
            />
            <Input
              label=" ()"
              placeholder="^/v1/messages$"
              value={formData.when.pathRegex || ""}
              onChange={(e) => updateField("when.pathRegex", e.target.value || undefined)}
              radius="lg"
            />
            <div className="md:col-span-2">
              <Input
                label=" ()"
                placeholder="claude-3"
                value={formData.when.modelRegex || ""}
                onChange={(e) => updateField("when.modelRegex", e.target.value || undefined)}
                radius="lg"
              />
            </div>
          </div>
        </CardBody>
      </Card>

      <Divider />

      {/*  */}
      <Card className="border border-gray-200 dark:border-gray-700">
        <CardBody className="space-y-3">
          <div className="flex items-center justify-between">
            <h4 className="text-lg font-bold"> (Ops)</h4>
            <Chip color="secondary" variant="flat" size="sm">
              {formData.ops.length} 
            </Chip>
          </div>
          <div className="text-sm text-gray-500">
            
          </div>

          <div className="space-y-3">
            {formData.ops.map((op, index) => (
              <Card key={index} className="bg-gray-50 dark:bg-gray-900/30 border border-gray-200 dark:border-gray-700">
                <CardBody className="space-y-3">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                      <Chip color="primary" size="sm" className="font-bold">
                         {index + 1}
                      </Chip>
                      <Chip color="default" variant="flat" size="sm" className="uppercase text-xs">
                        {op.type}
                      </Chip>
                    </div>
                    <Button
                      size="sm"
                      color="danger"
                      variant="light"
                      onPress={() => removeOp(index)}
                      className="hover:bg-red-50 dark:hover:bg-red-900/20"
                    >
                      
                    </Button>
                  </div>

                  <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
                    <div className="md:col-span-1">
                      <Select
                        label=""
                        selectedKeys={[op.type]}
                        onChange={(e) => updateOp(index, { type: e.target.value as PromptxyOpType })}
                        radius="lg"
                        isRequired
                      >
                        {OP_TYPE_OPTIONS.map((opt) => (
                          <SelectItem key={opt.value}>
                            {opt.label}
                          </SelectItem>
                        ))}
                      </Select>
                    </div>

                    {/*  */}
                    {(op.type === "set" || op.type === "append" || op.type === "prepend") && (
                      <div className="md:col-span-2">
                        <Textarea
                          label=""
                          placeholder="..."
                          value={op.text || ""}
                          onChange={(e) => updateOp(index, { text: e.target.value })}
                          radius="lg"
                          minRows={1}
                          isRequired
                        />
                      </div>
                    )}

                    {(op.type === "replace" || op.type === "delete") && (
                      <>
                        <div>
                          <Input
                            label=" ()"
                            placeholder=""
                            value={op.match || ""}
                            onChange={(e) => updateOp(index, { match: e.target.value || undefined })}
                            radius="lg"
                          />
                        </div>
                        <div>
                          <Input
                            label=" ()"
                            placeholder="pattern"
                            value={op.regex || ""}
                            onChange={(e) => updateOp(index, { regex: e.target.value || undefined })}
                            radius="lg"
                          />
                        </div>
                        <div>
                          <Input
                            label=" ()"
                            placeholder="gi"
                            value={op.flags || ""}
                            onChange={(e) => updateOp(index, { flags: e.target.value || undefined })}
                            radius="lg"
                          />
                        </div>
                        {op.type === "replace" && (
                          <div className="md:col-span-3">
                            <Input
                              label=""
                              placeholder=""
                              value={op.replacement || ""}
                              onChange={(e) => updateOp(index, { replacement: e.target.value })}
                              radius="lg"
                              isRequired
                            />
                          </div>
                        )}
                      </>
                    )}

                    {(op.type === "insert_before" || op.type === "insert_after") && (
                      <>
                        <div className="md:col-span-2">
                          <Input
                            label=""
                            placeholder="pattern"
                            value={op.regex || ""}
                            onChange={(e) => updateOp(index, { regex: e.target.value })}
                            radius="lg"
                            isRequired
                          />
                        </div>
                        <div>
                          <Input
                            label=" ()"
                            placeholder="gi"
                            value={op.flags || ""}
                            onChange={(e) => updateOp(index, { flags: e.target.value || undefined })}
                            radius="lg"
                          />
                        </div>
                        <div className="md:col-span-3">
                          <Textarea
                            label=""
                            placeholder=""
                            value={op.text || ""}
                            onChange={(e) => updateOp(index, { text: e.target.value })}
                            radius="lg"
                            minRows={1}
                            isRequired
                          />
                        </div>
                      </>
                    )}
                  </div>
                </CardBody>
              </Card>
            ))}
          </div>

          <Button
            variant="flat"
            onPress={addOp}
            className="w-full"
            color="primary"
          >
            + 
          </Button>
        </CardBody>
      </Card>

      <Divider />

      {/*  */}
      <Card className="border border-gray-200 dark:border-gray-700">
        <CardBody className="space-y-3">
          <h4 className="text-lg font-bold"></h4>
          <div className="flex flex-col gap-2">
            <Checkbox
              checked={formData.stop || false}
              onChange={(e) => updateField("stop", e.target.checked)}
            >
               (stop)
            </Checkbox>
            <Checkbox
              checked={formData.enabled !== false}
              onChange={(e) => updateField("enabled", e.target.checked)}
              color="success"
            >
              
            </Checkbox>
          </div>
        </CardBody>
      </Card>

      {/*  */}
      <div className="flex gap-2 justify-end pt-2">
        {onPreview && (
          <Button color="warning" variant="flat" onPress={handlePreview}>
            
          </Button>
        )}
        <Button variant="light" onPress={onCancel}>
          
        </Button>
        <Button
          color="primary"
          onPress={handleSave}
          isDisabled={!validation.valid}
          className="shadow-lg"
        >
          
        </Button>
      </div>
    </div>
  );
};
