import React, { useState } from "react";
import {
  Button,
  useDisclosure,
  Card,
  CardBody,
  Chip,
  Modal,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalFooter
} from "@heroui/react";
import { RuleList, RuleEditor } from "@/components/rules";
import { useRules, useSaveRules, useDeleteRule } from "@/hooks";
import { PromptxyRule } from "@/types";
import { validateRule } from "@/utils";

export const RulesPage: React.FC = () => {
  const { rules, isLoading, refetch } = useRules();
  const saveRulesMutation = useSaveRules();
  const deleteRuleMutation = useDeleteRule();

  const { isOpen, onOpen, onClose } = useDisclosure();
  const [editingRule, setEditingRule] = useState<PromptxyRule | null>(null);

  // 
  const handleNewRule = () => {
    const newRule: PromptxyRule = {
      id: `rule-${Date.now()}`,
      description: "",
      when: { client: "claude", field: "system" },
      ops: [{ type: "append", text: "\n\n" }],
      enabled: true,
    };
    setEditingRule(newRule);
    onOpen();
  };

  // 
  const handleEdit = (ruleId: string) => {
    const rule = rules.find((r) => r.id === ruleId);
    if (rule) {
      setEditingRule({ ...rule });
      onOpen();
    }
  };

  // 
  const handleDelete = async (ruleId: string) => {
    if (confirm(` ${ruleId} `)) {
      try {
        await deleteRuleMutation.mutateAsync(ruleId);
        refetch();
      } catch (error: any) {
        alert(`: ${error?.message}`);
      }
    }
  };

  // 
  const handleToggle = async (rule: PromptxyRule) => {
    const updatedRule = { ...rule, enabled: !rule.enabled };
    const updatedRules = rules.map((r) => (r.id === rule.id ? updatedRule : r));
    try {
      await saveRulesMutation.mutateAsync(updatedRules);
      refetch();
    } catch (error: any) {
      alert(`: ${error?.message}`);
    }
  };

  // 
  const handleSave = async (rule: PromptxyRule) => {
    const validation = validateRule(rule);
    if (!validation.valid) {
      alert(`:\n${validation.errors.join("\n")}`);
      return;
    }

    // 
    const existingIndex = rules.findIndex((r) => r.id === rule.id);
    let updatedRules: PromptxyRule[];

    if (existingIndex >= 0) {
      // 
      updatedRules = rules.map((r) => (r.id === rule.id ? rule : r));
    } else {
      // 
      updatedRules = [...rules, rule];
    }

    try {
      await saveRulesMutation.mutateAsync(updatedRules);
      onClose();
      setEditingRule(null);
      refetch();
    } catch (error: any) {
      alert(`: ${error?.message}`);
    }
  };

  const handleCancel = () => {
    onClose();
    setEditingRule(null);
  };

  return (
    <div className="p-6 space-y-6">
      {/*  */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
            
          </h1>
          <p className="text-sm text-gray-500 mt-1">
            
          </p>
        </div>
        <div className="flex gap-2">
          <Chip color="primary" variant="flat" size="sm">
            {rules.length} 
          </Chip>
        </div>
      </div>

      {/*  */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Card className="bg-gradient-to-br from-blue-50 to-blue-100 dark:from-blue-900/20 dark:to-blue-800/10 border-none">
          <CardBody className="p-4">
            <div className="text-sm text-blue-700 dark:text-blue-300 font-medium"></div>
            <div className="text-2xl font-bold text-blue-900 dark:text-blue-100">
              {rules.filter(r => r.enabled !== false).length}
            </div>
          </CardBody>
        </Card>
        <Card className="bg-gradient-to-br from-purple-50 to-purple-100 dark:from-purple-900/20 dark:to-purple-800/10 border-none">
          <CardBody className="p-4">
            <div className="text-sm text-purple-700 dark:text-purple-300 font-medium"></div>
            <div className="text-2xl font-bold text-purple-900 dark:text-purple-100">
              {rules.filter(r => r.enabled === false).length}
            </div>
          </CardBody>
        </Card>
        <Card className="bg-gradient-to-br from-green-50 to-green-100 dark:from-green-900/20 dark:to-green-800/10 border-none">
          <CardBody className="p-4">
            <div className="text-sm text-green-700 dark:text-green-300 font-medium"></div>
            <div className="text-2xl font-bold text-green-900 dark:text-green-100">
              {rules.length}
            </div>
          </CardBody>
        </Card>
      </div>

      {/*  */}
      <RuleList
        rules={rules}
        isLoading={isLoading}
        onEdit={handleEdit}
        onDelete={handleDelete}
        onToggle={handleToggle}
        onNewRule={handleNewRule}
      />

      {/*  */}
      <Modal
        isOpen={isOpen}
        onClose={handleCancel}
        placement="center"
        size="2xl"
        backdrop="blur"
        scrollBehavior="outside"
      >
        <ModalContent>
          <ModalHeader>
            {editingRule ? "" : ""}
          </ModalHeader>
          <ModalBody>
            <RuleEditor
              rule={editingRule}
              onSave={handleSave}
              onCancel={handleCancel}
            />
          </ModalBody>
        </ModalContent>
      </Modal>
    </div>
  );
};
